(function () {
    'use strict';

    angular.module('starter').filter('hashtag', hashtag);

    function hashtag($filter) {
        return function (text, target) {
            if (!text) return text;

            var replacedText = $filter('linky')(text, target);

            var targetAttr = "";
            if (angular.isDefined(target)) {
                targetAttr = target;
            }

            // replace #hashtags and send them to twitter
            var replacePattern1 = /(^|\s)#(\w*[a-zA-Z_]+\w*)/gim;
            replacedText        = text.replace(replacePattern1, '$1<a href="https://photogramapp.com/search?q=%23$2"' + targetAttr + '>#$2</a>');

            // replace @mentions but keep them to our site
            var replacePattern2 = /(^|\s)\@(\w*[a-zA-Z_]+\w*)/gim;
            replacedText        = replacedText.replace(replacePattern2, '$1<a href="#/tab/home/$2" >@$2</a>');

            return replacedText;
        };
    }

})();


(function () {
    'use strict';

    angular.module('starter').filter('words', words);

    function words() {

        ////////////////

        return function (input, words) {
            if (isNaN(words)) {
                return input;
            }
            if (words <= 0) {
                return '';
            }
            if (input) {
                var inputWords = input.split(/\s+/);
                if (inputWords.length > words) {
                    input = inputWords.slice(0, words).join(' ') + '\u2026';
                }
            }
            return input;
        };
    }

})();


(function () {
    'use strict';

    angular.module('starter').directive('signinModal', signinModalDirective);

    function signinModalDirective($ionicModal, Loading, User, $state, Toast, UserForm, AppConfig, $rootScope) {
        return {
            restrict: 'A',
            link    : signinModalLink,
        };

        function signinModalLink($scope, elem, attr) {
            elem.bind('click', function () {

                $scope.formFields  = UserForm.login;
                $scope.routeLogged = AppConfig.routes.home;

                $scope.form = {
                    username: '',
                    password: ''
                };

                $scope.submitLogin = function (rForm, data) {
                    var form = angular.copy(data);
                    if (!rForm.$invalid) {
                        Loading.start();
                        User.signIn(form).then(function (data) {
                            console.log(data);
                            $rootScope.currentUser = data;
                            $state.go($scope.routeLogged, {
                                clear: true
                            });
                            $scope.closeModal();
                            Loading.end();
                        }).catch(function (resp) {
                            Toast.alert({
                                title: 'Error',
                                text : 'Incorrect username or password'
                            });
                            Loading.end();
                        });
                    } else {
                        return false;
                    }
                };

                $ionicModal.fromTemplateUrl('app/directive/SigninModalDirective.html', {
                    scope          : $scope,
                    animation      : 'slide-in-up',
                    focusFirstInput: true,
                }).then(function (modal) {
                    $scope.modal = modal;
                    $scope.modal.show();
                });

                $scope.closeModal = function () {
                    $scope.modal.hide();
                };
                // Cleanup the modal when we're done with it!
                $scope.$on('$destroy', function () {
                    $scope.modal.remove();
                });
            });
        }
    }

})();
(function () {
    'use strict';

    angular.module('starter').directive('signupModal', signupModalDirective);

    function signupModalDirective(UserForm, AppConfig, Loading, User, $state, Toast, $ionicModal, $rootScope) {
        return {
            restrict: 'A',
            link    : signupModalLink,
        };

        function signupModalLink($scope, elem, attr) {
            elem.bind('click', function () {

                $scope.formFields  = UserForm.register;
                $scope.routeLogged = AppConfig.routes.home;

                $scope.form = {
                    username: '',
                    email   : '',
                    password: ''
                };

                $scope.submitRegister = function (rForm, data) {
                    if (rForm.$valid) {
                        Loading.start();
                        var form = angular.copy(data);
                        User.signUp(form).then(function (resp) {
                            console.log(resp);

                            // After register, login
                            User.signIn({
                                username: form.username,
                                password: form.password
                            }).then(function (data) {
                                console.log(data);
                                $rootScope.currentUser = Parse.User.current();
                                $state.go('user.avatar', {
                                    clear: true
                                });
                                Loading.end();
                                $scope.closeModal();
                            }).catch(function (resp) {
                                console.log(resp);
                                Toast.alert({
                                    title: 'Alert',
                                    text : resp.error
                                });
                                Loading.end();
                            });
                        }).catch(function (resp) {
                            console.log(resp);
                            Toast.alert({
                                title: 'Alert',
                                text : resp.error
                            });
                            Loading.end();
                        });
                    }


                };

                $ionicModal.fromTemplateUrl('app/directive/SignupModalDirective.html', {
                    scope          : $scope,
                    animation      : 'slide-in-up',
                    focusFirstInput: true,
                }).then(function (modal) {
                    $scope.modal = modal;
                    $scope.modal.show();
                });

                $scope.closeModal = function () {
                    $scope.modal.hide();
                };
                // Cleanup the modal when we're done with it!
                $scope.$on('$destroy', function () {
                    $scope.modal.remove();
                });
            });
        }
    }

})();
