(function () {
  'use strict';
  angular.module('starter').factory('Share', PhotogramShareFactory);

  function PhotogramShareFactory(AppConfig, $q, $ionicActionSheet, Toast, $cordovaSocialSharing) {

    var message = {
      title  : ('Join me from ') + AppConfig.app.name + '!',
      subject: ("I'm at ") + AppConfig.app.name + '!. ' + (
        'Install the application and follow me!') + ' ' + AppConfig.app.url,
      image  : AppConfig.app.image,
      link   : AppConfig.app.url
    };

    return {
      share: share,
      open : open
    };

    function share(social, option) {
      var defer = $q.defer();

      function success(resp) {
        Toast.alert({
          title: ('Thanks'),
          text : ('Thank you for sharing!!')
        });
        defer.resolve(resp);
      }

      function error(err) {
        console.error(err);
        defer.reject();
      }

      var detail = option ? option : message;

      switch (social) {
        case 'instagram':
          window
            .plugins
            .socialsharing
            .shareViaInstagram(message.text, message.image, message.link)
            .then(success, error);
          break;

        case 'facebook':
          $cordovaSocialSharing
            .shareViaFacebook(detail.text, detail.image, detail.link)
            .then(success, error);
          break;

        case 'twitter':
          $cordovaSocialSharing
            .shareViaTwitter(detail.text, detail.image, detail.link)
            .then(success, error);
          break;

        case 'whatsapp':
          $cordovaSocialSharing
            .shareViaWhatsApp(detail.text, detail.image, detail.link)
            .then(success, error);
          break;

        case 'email':
          $cordovaSocialSharing
            .shareViaEmail(detail.title, detail.subject ? detail.subject : detail.title)
            .then(success, error);
          break;
      }

      return defer.promise;
    }

    function open() {
      var modal = $ionicActionSheet
        .show({
          buttons      : [{
            text: '<i class="icon ion-social-instagram"></i>' + ('Instagram')
          }, {
            text: '<i class="icon ion-social-facebook"></i>' + ('Facebook')
          }, {
            text: '<i class="icon ion-social-twitter"></i>' + ('Twitter')
          }, {
            text: '<i class="icon ion-social-whatsapp"></i>' + ('Whatsapp')
          }, {
            text: '<i class="icon ion-email"></i>' + ('Email')
          }],
          titleText    : ('Share'),
          cancelText   : ('Cancel'),
          cancel       : function () {
            return false;
          },
          buttonClicked: function (index) {
            console.log(index);
            switch (index) {
              case 0:
                share('instagram');
                break;
              case 1:
                share('facebook');
                break;
              case 2:
                share('twitter');
                break;
              case 3:
                share('whatsapp');
                break;
              case 4:
                share('email');
                break;
            }
            modal();
            //share(index);
          }
        });

    }
  }

})();

(function () {
  'use strict';

  angular.module('starter').factory('Toast', ToastFactory);

  function ToastFactory($cordovaToast, $ionicPopup) {

    return {
      alert  : alert,
      confirm: confirm
    };

    function alert(params) {
      if (window.cordova) {
        $cordovaToast.show(params.text, 'short', 'bottom');
      } else {
        $ionicPopup.alert({
          title   : params.title,
          template: params.text,
          okType  : 'button button-clear button-assertive',
        });
      }
    }

    function confirm(title, msg) {
      return $ionicPopup.confirm({
        title   : title,
        template: msg
      });
    }
  }


})();

(function () {
    'use strict';
    angular.module('starter').factory('User', UserFactory);

    function UserFactory($q, $cordovaDevice, ParseCloud) {

        var User = Parse.User.extend({}, {
            profile               : function (username) {
                return ParseCloud.run('profile', {username: username})
            },
            list                  : function (username) {
                return ParseCloud.run('listUsers')
            },
            getFollowers: function (username) {
                return ParseCloud.run('getFollowers')
            },
            signIn                : function (obj) {
                var defer = $q.defer();

                Parse.User.logIn(obj.username, obj.password, {
                    success: function (currentUser) {

                        // device
                        var device;
                        if (window.cordova) {
                            device = {
                                device  : $cordovaDevice.getDevice(),
                                cordova : $cordovaDevice.getCordova(),
                                model   : $cordovaDevice.getModel(),
                                platform: $cordovaDevice.getPlatform(),
                                uuid    : $cordovaDevice.getUUID(),
                                version : $cordovaDevice.getVersion()
                            };
                        } else {
                            device = {
                                device  : window.navigator.userAgent.match(/(?:Chrom(?:e|ium)|Firefox)\/([0-9]+)\./)[0],
                                cordova : '',
                                model   : window.navigator.userAgent.match(/(?:Chrom(?:e|ium)|Firefox)\/([0-9]+)\./)[0],
                                platform: window.navigator.platform,
                                uuid    : '',
                                version : window.navigator.userAgent.match(/(?:Chrom(?:e|ium)|Firefox)\/([0-9]+)\./)[1]
                            };
                        }

                        console.log(currentUser);
                        console.log(device);

                        User.update(device).then(function () {
                            defer.resolve(currentUser);
                        }).catch(defer.reject)

                        console.log('updateUser', device);
                        //user.save(defer.resolve, defer.reject);
                    },
                    error  : defer.reject
                });

                return defer.promise;
            },
            signUp                : function (data) {
                var defer = $q.defer();
                var user  = new Parse.User()
                    .set({'name': data.name})
                    .set({'username': data.username})
                    .set({'email': data.email})
                    .set({'password': data.password})
                    .set({'roleName': 'User'});

                var acl = new Parse.ACL();
                acl.setPublicReadAccess(false);
                acl.setPublicWriteAccess(false);
                user.setACL(acl);
                user.save(null, {
                    success: defer.resolve,
                    error  : defer.reject
                });

                return defer.promise;
            },
            signInViaFacebook     : function (authData) {
                var defer      = $q.defer();
                var expiration = new Date();
                expiration.setSeconds(expiration.getSeconds() + authData.authResponse.expiresIn);
                expiration = expiration.toISOString();

                var facebookAuthData = {
                    'id'             : authData.authResponse.userID,
                    'access_token'   : authData.authResponse.accessToken,
                    'expiration_date': expiration
                };
                Parse.FacebookUtils.logIn(facebookAuthData, {
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            logOut                : function () {
                var defer = $q.defer();
                Parse.User.logOut().then(defer.resolve, defer.reject);
                return defer.promise;
            },
            findByEmail           : function (email) {
                return ParseCloud.run('findUserByEmail', {email: email});
            },
            updateWithFacebookData: function (data) {
                var defer = $q.defer();
                ParseCloud.run('saveFacebookPicture', {}).then(function () {
                    var user = Parse.User.current();
                    user.set({'email': data.email});
                    user.set({'username': data.email});
                    user.set({'name': data.name});
                    user.setACL(new Parse.ACL(user));
                    user.save(null, {
                        success: function () {
                            user.fetch().then(function (userFetched) {
                                defer.resolve(userFetched);
                            }, function (error) {
                                defer.reject(error);
                            });
                        }
                    });
                }).catch(defer.reject);
                return defer.promise;
            },
            getPublicData         : function (user) {
                console.log(user);
                var defer = $q.defer();
                new Parse.Query('UserData').equalTo('user', user).first().then(function (userData) {
                    if (userData) {
                        defer.resolve(userData);
                    } else {
                        defer.reject(Parse.Promise.error({
                            code   : 1,
                            message: 'User Data not found'
                        }));
                    }
                }, defer.reject);
                return defer.promise;
            },
            recoverPassword       : function (email) {
                var defer = $q.defer();
                Parse.User.requestPasswordReset(email, {
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            destroy               : function () {
                var defer = $q.defer();
                Parse.User.current().destroy().then(defer.resolve, defer.reject);
                return defer.promise;
            },
            setPhoto              : function (parseFile) {
                var defer = $q.defer();
                var user  = Parse.User.current();
                user.set({'photo': parseFile});
                user.save(null, {
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            follow                : function (userId) {
                return ParseCloud.run('followUser', {userId: userId});
            },
            all                   : function (params) {
                return ParseCloud.run('getUsers', params);
            },
            validateUsername: function (input) {
                return ParseCloud.run('validateUsername', {username: input});
            },
            validateEmail   : function (input) {
                return ParseCloud.run('validateEmail', {email: input});
            },
            findUsername    : function (username) {
                var defer = $q.defer();
                new Parse.Query(this).equalTo('username', username).first({
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            update          : function (params) {
                var defer = $q.defer();
                var user  = Parse.User.current();
                angular.forEach(params, function (value, key) {
                    user.set(key, value);
                });
                user.save({
                    success: defer.resolve,
                    error  : defer.reject
                });
                return defer.promise;
            },
            delete          : function (data) {
                return ParseCloud.run('destroyUser', data);
            },
            fetch           : function () {
                var defer = $q.defer();
                if (Parse.User.current()) {
                    Parse.User.current().fetch().then(defer.resolve, defer.reject);
                } else {
                    defer.reject();
                }
                return defer.promise;
            }

        });

        Object.defineProperty(User.prototype, 'name', {
            get: function () {
                return this.get('name');
            },
            set: function (val) {
                this.set('name', val);
            }
        });

        Object.defineProperty(User.prototype, 'username', {
            get: function () {
                return this.get('username');
            },
            set: function (val) {
                this.set('username', val);
            }
        });

        Object.defineProperty(User.prototype, 'status', {
            get: function () {
                return this.get('status');
            },
            set: function (val) {
                this.set('status', val);
            }
        });

        Object.defineProperty(User.prototype, 'gender', {
            get: function () {
                return this.get('gender');
            },
            set: function (val) {
                this.set('gender', val);
            }
        });

        Object.defineProperty(User.prototype, 'email', {
            get: function () {
                return this.get('email');
            },
            set: function (val) {
                this.set('email', val);
            }
        });

        Object.defineProperty(User.prototype, 'photo', {
            get: function () {
                return this.get('photo');
            },
            set: function (val) {
                this.set('photo', val);
            }
        });

        Object.defineProperty(User.prototype, 'photoThumb', {
            get: function () {
                return this.get('photoThumb');
            },
            set: function (val) {
                this.set('photoThumb', val);
            }
        });

        Object.defineProperty(User.prototype, 'roleName', {
            get: function () {
                return this.get('roleName');
            },
            set: function (val) {
                this.set('roleName', val);
            }
        });

        return User;

    }

})();

(function () {
    'use strict';

    /**
     * @ngdoc directive
     * @name modalLanguage
     *
     * @description
     * _Please update the description and restriction._
     *
     * @restrict A
     * */

    var path = 'app/component/ion-language';

    angular
        .module('ion-language')
        .directive('ionLanguage', ionLanguageDirective);

    function ionLanguageDirective(AppConfig, $translate, $timeout, Loading, $ionicModal) {
        return {
            restrict: 'E',
            link: modalLanguageLink,
            templateUrl: path + '/ion-language.html'
        };

        function modalLanguageLink(scope, elem, attr) {


            scope.languages = AppConfig.locales;
            scope.language  = scope.languages.filter(function (item) {
                return item.code === $translate.use()
            })[0];

            console.log(scope.language);

            elem.bind('click', openModal);

            function openModal() {

                window.StatusBar.styleDefault();

                scope.closeModal = function () {
                    scope.modal.hide();
                    // scope.modal.remove();
                    window.StatusBar.styleLightContent();
                };


                scope.searchValue = '';

                scope.selectLanguage = function (language) {
                    console.log(language);
                    $translate.use(language.code);
                    moment.locale(language.code);
                    scope.language = language;
                    Loading.start();
                    scope.closeModal();
                    $timeout(function () {
                        Loading.end();
                    }, 1000);
                };

                $ionicModal
                    .fromTemplateUrl(path + '/modal-language.html', {
                        scope: scope,
                        focusFirstInput: true
                    })
                    .then(function (modal) {
                        scope.modal = modal;
                        scope.modal.show();

                    });
            }
        }
    }

})();

